html { 
    --border-color: #FF3EA5;
    --pepto-pink: #f68fe0;
    --other-pink: #F218A0;
    --baby-pink: #F8C7CC;
    --yellow: #F2D20C;
    --bright-green: #B6F20C;

    --green-p1: #DAFF7D;
    --green-p2: #B2EF9B;
    --rssn-grn: #82a183;

    --t1-blk: #000000;
    --t1-bby-pow: #FFFFFC;
    --t1-lime: #B6F20C;
    --t1-amber: #FF7F11;
    --t1-red-orng: #FF3F00;

    --t2-dar: #DAFF7D;
    --t2-granny: #B2EF9B;
    --t2-rssn-grn: #82A183;
    --t2-mrgold: #ECA72C;
    --t2-flame: #EE5622;
    --t2-indep: #505168;
    --t2-drk-prp: #27233A;

    --bkgnd: var(--t2-dar);
    --grn-grad: linear-gradient(180deg, var(--t2-dar) 0%, var(--t2-granny) 50%, var(--t2-rssn-grn) 100%);
}

body {
    display: flex;
    margin: 0;
    padding: 0;
    font-family: HK Grotesk,system-ui,-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,sans-serif;
    font-weight: bold;
    font-size: 20 pt;
}

.big-papa {
    display:flex;
    flex-flow:column nowrap;
    flex-basis: auto;
    width: auto;
    height: auto;
	margin: 0;
	padding: 0;
	background: var(--bckgnd);
}

.navbar {
	display: flex;
    flex-flow: row nowrap;
	align-items: center;
	justify-content: center;
	width: 70%;
	top: 0;
    background: var(--t2-granny);
    box-shadow: 4px 4px var(--t2-rssn-grn);
}

.footer {
    display: flex;
    align-self: center;
    margin: auto;
	background: none;
}

.special-h2 {
    color: var(--t2-flame);
    text-shadow: 2px 2px var(--t2-indep);
}

.nav-list {
	display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
	align-items: center;
}

.nav-list li {
	list-style: none;
	padding: 26px 30px;
}

.nav-list li a {
	text-decoration: none;
    color: var(--other-pink);
}

.nav-list li a:hover {
	text-shadow: 1px 1px var(--yellow), -1px -1px var(--yellow);
}


.box-main {
	display: flex;
	justify-content: center;
	align-items: center;
	color: black;
	max-width: 80%;
	margin: auto;
	height: 80%;
}

.first-half {
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.text-big {
	font-weight: 800;
	font-size: 90px;
    color: var(--other-pink);
    text-shadow: 2px 2px 2px var(--yellow);
}

.text-small {
	font-size: 18px;
}


.section {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.text-footer {
	text-align: center;
	padding: 30px 0;
	font-family: 'Ubuntu', sans-serif;
	display: flex;
	justify-content: center;
	color: var(--other-pink);
}